import React, {useEffect, useState} from 'react'
import listService from '../services/listService'
import userService from '../services/userService'
import {LOCAL_STORAGE_ID} from '../utils/constants'

const RegisterForm = () => {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [registered, setRegistered] = useState(false)
  
    useEffect(() => {
      const loggedUserJSON = window.localStorage.getItem(LOCAL_STORAGE_ID)
      if(loggedUserJSON) {
          const user = JSON.parse(loggedUserJSON)
          listService.setToken(user.token)
      }
    }, [])

    const handleRegistration = async (event) => {
        event.preventDefault()
        try {
          const user = await userService.registerUser(username, password)
          setRegistered(true)
          //Tell user to log in
        } catch (exception) {
          console.log(exception)
        }
      }
     
    const loginForm = () => {
      return (
        <form onSubmit={handleRegistration}>
          <div>
            username
              <input type="text" value={username} name="Username" onChange={({ target }) => setUsername(target.value)}/> 
          </div>  
          <div>
            password
              <input type="text" value={password} name="Password" onChange={({ target }) => setPassword(target.value)}/>
          </div>    
          <button type="submit">login</button>
        </form>
      )
    }


    return (
      <div>
        {registered ? <div>Success! Log in with your new credentials.</div> : loginForm()}
      </div>
    )
  }
  
  export default RegisterForm